import styled from "styled-components";
import { Animated } from "react-animated-css";
import { AnimationOnScroll } from "react-animation-on-scroll";
import moonImg from "../images/moon.png";
import { mobile } from "../responsive";

const Container = styled.div`
  position: relative;
  height: 100vh;
  z-index: 2;
  background-color: transparent;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-left: 20%;
  padding-right: 20%;
`;
const BackgroundContainer = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  margin: 0 0;
  padding: 0;
  z-index: -1;
`;
const BackgroundContainer2 = styled.div`
  position: relative;
  width: 100vw;
  overflow: hidden;
`;

const Moon = styled.img`
  width: 100vw;
  transform: translate(-7vw, 0vh);
  ${mobile({ width: "200vw" })};
`;
Moon.defaultProps = {
  src: moonImg,
};

const Title = styled.h1`
  background-color: transparent;
`;
const Content = styled.p`
  background-color: transparent;
`;

const home = () => {
  const animatedStyle = {
    backgroundColor: "transparent",
    // padding: "0",
    // margin: "0",
    // // position: "relative",
    // top: "0",
    // left: "0",
    // // width: "100vw",
  };
  return (
    <Container id="#">
      <BackgroundContainer>
        <BackgroundContainer2>
          <Moon />
        </BackgroundContainer2>
      </BackgroundContainer>
      <Animated
        animationIn="fadeInLeft"
        animationOut="fadeOut"
        isVisible={true}
        style={animatedStyle}
      >
        <AnimationOnScroll
          animateIn="fadeIn"
          animateOut="fadeOut"
          style={animatedStyle}
          delay="1"
          initiallyVisible="true"
        >
          <Title className="abril">Welcome.</Title>
          <Content>
            Hi, my name is Chris and I am a Software Engineering Technology
            Graduate based in Vancouver, BC. My current interests are in UX/UI
            design, database and full-stack web development.
          </Content>
        </AnimationOnScroll>
      </Animated>
    </Container>
  );
};

export default home;
