import React from "react"
import styled from "styled-components"
import { PopupButton } from "react-calendly"
import { AnimationOnScroll } from "react-animation-on-scroll"
import seaImg from "../images/sea.png"

const Container = styled.div`
  position: relative;
  height: 100vh;
  z-index: 2;
  background-color: transparent;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-left: 20%;
  padding-right: 20%;
`
const BackgroundContainer = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0 0;
  padding: 0;
  z-index: -1;
`
const BackgroundContainer2 = styled.div`
  position: relative;
  width: 100vw;
  overflow: hidden;
`

const Sea = styled.img`
  width: 100vw;
  bottom: 0;
  left: 0;
`

Sea.defaultProps = {
  src: seaImg,
}

const Title = styled.h1`
  background-color: transparent;
`
const Content = styled.p`
  background-color: transparent;
`
const EmailButton = styled.button`
  cursor: pointer;
  width: 100%;
  // background: #transparent;
  font-family: inherit;
  padding: 0.6em 1.3em;
  font-weight: 900;
  font-size: 18px;
  border: 3px solid #3f62af;
  border-radius: 0.4em;
  box-shadow: #3f62af 0.1em 0.1em;
  &:hover {
    transform: translate(-0.05em, -0.05em);
    box-shadow: #3f62af 0.15em 0.15em;
  }
  &:active {
    transform: translate(0.05em, 0.05em);
    box-shadow: #3f62af 0.05em 0.05em;
  }
`
const EmailLink = styled.a`
  background-color: transparent;
  text-decoration: none;
  width: 100%;
  height: 100%;
`

const Contact = () => {
  const Style = {
    cursor: "pointer",
    width: "100%",
    fontFamily: "inherit",
    padding: "0.6em 1.3em",
    fontWeight: "900",
    fontSize: "18px",
    border: "3px solid #3f62af",
    borderRadius: "0.4em",
    boxShadow: "#3f62af 0.1em 0.1em",
    onHover: {
      opacity: 0,
    },
  }
  const animatedStyle = {
    backgroundColor: "transparent",
  }

  return (
    <Container id='contact'>
      <BackgroundContainer>
        <BackgroundContainer2>
          <Sea />
        </BackgroundContainer2>
      </BackgroundContainer>
      <AnimationOnScroll
        animateIn='fadeIn'
        animateOut='fadeOut'
        style={animatedStyle}
      >
        <Title className='abril'>Contact</Title>
        <br></br>
        <Content>
          <EmailLink href='mailto: tapnioc@sheridancollege.ca'>
            {" "}
            <EmailButton> Email</EmailButton>
          </EmailLink>

          <br></br>
          <br></br>
          <PopupButton
            url='https://calendly.com/tapnioc/30min'
            /*
             * react-calendly uses React's Portal feature (https://reactjs.org/docs/portals.html) to render the popup modal. As a result, you'll need to
             * specify the rootElement property to ensure that the modal is inserted into the correct domNode.
             */
            rootElement={document.getElementById("root")}
            text='Book a virtual meeting'
            styles={Style}
          />
        </Content>
      </AnimationOnScroll>
    </Container>
  )
}

export default Contact
