import styled from "styled-components"
import { mobile } from "../responsive"
import ProjectIcons from "./projectIcons"
const CardInfo = styled.div`
  position: relative;
  z-index: 3;
  color: #f5f5f5;
  background-color: #3f62af;
  opacity: 0;
  transform: translateY(20%);
  transition: 0.5s;
  border-radius: 10px;
`
const CardFront = styled.p`
  background: #3f62af;
  bottom: 50%;
  position: absolute;
  justify-content: center;
  width: 100%;
`

const CardImg = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 10px;
  top: 0;
  left: 0;
  background: #3f62af;
  text-align: center;
`
const Card = styled.div`
  margin: 5px;
  position: relative;
  overflow: hidden;
  width: 190px;
  height: 254px;
  border-radius: 10px;
  padding: 1.9rem;
  display: flex;
  align-items: center;
  transition: 0.4s ease-out;
  box-shadow: 0px 7px 20px rgba(43, 8, 37, 0.2);
  &:before {
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    z-index: 2;
    transition: 0.5s;
  }
  &:hover {
    ${CardInfo} {
      opacity: 1;
      transform: translateY(0);
    }
    ${CardFront} {
      content: "";
      display: none;
      transition: 2s;
    }
  }
  &:hover:before {
    background: transparent;
    opacity: 1;
  }
  ${mobile({ flexDirection: "column", width: "70%", height: "50%" })}
`

const TextTitle = styled.p`
  font-size: 1.5rem;
  font-weight: 500;
  background-color: #3f62af;
`
const TextBody = styled.p`
  letter-spacing: 1px;
  font-size: 0.9rem;
  margin: 5px 0 15px 0;
  background-color: #3f62af;
  ${mobile({ fontSize: "0.7rem" })}
`
const CardButton = styled.button`
  cursor: pointer;
  padding: 0.6rem;
  outline: none;
  border: none;
  border-radius: 4px;
  background: white;
  color: black;
  font-weight: bold;
  transition: 0.4s ease;
  &:hover:before {
    opacity: 1;
  }
  &:hover {
    background: #5b8cfc;
    color: #f5f5f5;
  }
`

const Project = ({ item }) => {
  return (
    <Card>
      <CardImg>
        <CardFront>
          {item.title}
          <br></br>
          {item.icons.map((icon, i) => (
            <ProjectIcons icon={icon} key={i} />
          ))}
        </CardFront>
      </CardImg>

      <CardInfo>
        <TextTitle>
          {item.title}
          <br></br>
          {item.icons.map((icon, i) => (
            <ProjectIcons icon={icon} key={i} />
          ))}
        </TextTitle>
        <TextBody>{item.desc}</TextBody>
        <a href={item.link} target='_blank' rel='noreferrer'>
          {" "}
          <CardButton>{item.buttonText}</CardButton>
        </a>
      </CardInfo>
    </Card>
  )
}

export default Project
